import React from 'react';
import { Label} from 'react-bootstrap';

export const Languages = () => {
    return(
        <div>
            <h4>Languages</h4>
            <h3>
                <a href='/'><Label bsStyle="primary">English</Label></a> {' '}
                <a href='/homech'><Label bsStyle="danger">中文</Label></a>
            </h3>
            <h3>
                <a href='/'><Label bsStyle="warning">français</Label></a> {' '}
                <a href='/'><Label bsStyle="success">日本語</Label></a>
            </h3>
        </div>
        
    )
}


